									<el-form-item style="display: inline-block;" label="证件类型：">
								    	<el-select v-model="formData.bothTopExpertPersonForm.idCardType" placeholder="请选择">
										    <el-option
										      v-for="item in areaOptions"
										      :key="item.id"
										      :label="item.label"
										      :value="item.id">
										    </el-option>
										</el-select>
								  	</el-form-item>
								  	<el-input class="dt-input flex-item1 max-width-1" v-model="formData.bothTopExpertPersonForm.idCard" style="padding-left: 20px;" placeholder="请输入"></el-input>
								</div>
