							    </el-table-column>
							    <el-table-column
							      prop="postDoc"
							      label="博士后">
							    </el-table-column>
							</el-table>
						</div>
					</div>
					<!-- 项目内容 -->
					<div v-show="activeIdx === 3">
						<div class="all-fold-box" @click="isFold4 = !isFold4">
