							<span v-else><i class="el-icon-arrow-down"></i>全部展开</span>
						</div>
						<h5 class="sub-title"><span class="bar"></span>项目摘要</h5>
						<p class="sub-title-detail">简要说明项目立项的必要性、研究目标、技术方案、预期成果、相关基础条件及协同机制等（限1000字以内）。</p>
						<dt-textarea :value.sync="formData.projectAbstract" :isFold="isFold4" :maxNumber="1000"></dt-textarea>
			  			<h5 class="sub-title"><span class="bar"></span>立项的必要性</h5>

						<p class="sub-title-detail">阐述立项的必要性、紧迫性以及国内外开展研究及进展情况（限2000字以内）</p>
						<dt-textarea :value.sync="formData.approvalNecessay" :isFold="isFold4" :maxNumber="2000"></dt-textarea>
			  			
			  			<h5 class="sub-title"><span class="bar"></span>预期取得的重大成果及意义</h5>
						<p class="sub-title-detail">（一）项目成果的呈现形式及描述（限500字以内）</p>

						<dt-textarea :value.sync="formData.achievementForm" :isFold="isFold4" :maxNumber="500"></dt-textarea>
			  			<p class="sub-title-detail">（二）项目成果的预期经济、社会效益（限500字以内）</p>
						<dt-textarea :value.sync="formData.achievementBenefit" :isFold="isFold4" :maxNumber="500"></dt-textarea>
			  			
			  			<h5 class="sub-title"><span class="bar"></span>项目研究内容及技术方案</h5>
						<p class="sub-title-detail">（一）主要研究内容（限1500字以内）</p>
						<dt-textarea :value.sync="formData.researchContentMain" :isFold="isFold4" :maxNumber="1500"></dt-textarea>

			  			<p class="sub-title-detail">（二）拟采取的研究方法、技术路线及其可行性分析（技术路线可以图表形式显示，限2000字以内）。</p>
						<dt-textarea :value.sync="formData.researchContentMethod" :isFold="isFold4" :maxNumber="2000"></dt-textarea>

			  			<p class="sub-title-detail">（三）国际合作与交流方案。（包括“引进来”和“走出去”，限1000字以内）</p>
						<dt-textarea :value.sync="formData.researchContentInterflow" :isFold="isFold4" :maxNumber="1000"></dt-textarea>

			  			<p class="sub-title-detail">（四）成果转化应用和科普方案（限1000字以内）。</p>
						<dt-textarea :value.sync="formData.researchContentUsePlan" :isFold="isFold4" :maxNumber="1000"></dt-textarea>
			  			
			  			<p class="sub-title-detail">（五）项目预期的主要创新点（围绕基础前沿、共性关键技术或应用试验等层面简要阐述，应包括该项创新的基本形态及其前沿性、时效性等，并说明是否具备方法、理论和知识产权特征。每项创新点的描述限200字以内）</p>
						<dt-textarea :value.sync="formData.researchContentInnovate" :isFold="isFold4" :maxNumber="2000"></dt-textarea>
					</div>
					<!-- 项目目标和任务 -->
