						<p class="sub-title-detail">包括科学研究、人才聚集培养、国际合作、技术体系建设、转化应用和科学普及（如适用）等方面，应有量化指标，可考核，可比较，可追溯（限1000字以内）。</p>
						<dt-textarea :value.sync="formData.projectTarget" :maxNumber="1000"></dt-textarea>
			  			<h5 class="sub-title"><span class="bar"></span>项目的年度任务、考核指标和时间节点</h5>
						<div class="table-box">
							<dt-table :tableData.sync="formData.taskPreYearList" :thead="taskPreYearListThead"></dt-table>
						</div>
						<h5 class="sub-title"><span class="bar"></span>项目任务分解情况和各任务之间的逻辑关系图示 </h5>
						<p class="sub-title-detail">围绕项目目标，根据需要可对项目目标进行任务分解，要求对分解的各任务能够相对独立表达、独立测度和独立评价，各任务之间内容和单位、团队、人员不得交叉重复。</p>
						<div class="table-box">
							<dt-table :tableData.sync="formData.taskDecomposition" :thead="taskDecompositionHead"></dt-table>				
						</div>
						<h5 class="sub-title"><span class="bar"></span>联合申报单位任务分工情况</h5>
						<div class="table-box">
							<dt-table :tableData.sync="formData.jointUnitTask" :thead="jointUnitTaskHead"></dt-table>
						</div>
					</div>
					<!-- 项目其他信息 -->
					<div v-show="activeIdx === 5">
