							<span v-if="!isFold6"><i class="el-icon-arrow-up"></i>全部收起</span>
							<span v-else><i class="el-icon-arrow-down"></i>全部展开</span>
						</div>
						<h5 class="sub-title"><span class="bar"></span>基础条件和优势</h5>
						<p class="sub-title-detail">（一）项目参与单位的选择原因及其优势（限800字以内）</p>
						<dt-textarea :value.sync="formData.superiorityDeptChoose" :isFold="isFold6" :maxNumber="1000"></dt-textarea>

			  			<p class="sub-title-detail">（二）项目牵头和参加单位与课题实施相关的实力和基础，近5年在本领域承担相关科研项目、取得的业绩和成就等（限1500字以内）。</p>
						<dt-textarea :value.sync="formData.superiorityDeptBasic" :isFold="isFold6" :maxNumber="1500"></dt-textarea>
			  			<p class="sub-title-detail">（三）申报单位相关科研条件支撑状况（包括实验平台、大型仪器设备、依托院校级及以上研发基地情况，限500字以内）</p>
						<dt-textarea :value.sync="formData.superiorityDeptSuport" :isFold="isFold6" :maxNumber="1000"></dt-textarea>
			  			<p class="sub-title-detail">（四）国际合作单位及团队情况（限300字以内）。</p>
						<dt-textarea :value.sync="formData.superiorityDeptAbroad" :maxNumber="300" :isFold="isFold6"></dt-textarea>

