			        	<h5>一、项目基本信息</h5>
						<ul>
							<li>1.项目名称应以中国医学科学院医学与健康科技创新工程（以下简称创新工程）批复的名称为准。</li>
							<li>2.牵头单位为项目组织实施的责任单位，目标任务书由牵头单位组织填写，编写人员应客观、真实地填报有关数据，尊重他人知识产权，遵守国家有关知识产权法规。</li>
							<li>3.对于伪造、篡改科学数据，抄袭他人著作、论文或者剽窃他人科研成果等科研不端行为，一经查实，将记入信用记录。</li>
							<li>4.系统默认正文小4号宋体、1.5倍行距，双面打印，简单装订，系统自动生成申报书将不改变表格格式和结构。</li>
							<li>5.项目须三家及以上所院联合申报，只能由一家科研院所牵头。</li>
							<li>6.每个项目只设一名首席专家。根据需要可以设一名共同首席专家。项目骨干人员原则上应为院校所属单位在编科研人员和博士后，研究生和编制外聘用人员一般不应列入。</li>
							<li>7.专业应填目前所从事领域的专业，按二级学科专业填写，参照国家自然科学基金委的专业分类目录。 </li>
